@import "../../_globalColor";

.skill {
  line-height: 3.5vw;
}

.skill-bars {
  font-size: 28px;
  width: 40%;
}

.skills-container {
  display: flex;
  width: 90%;
  padding: 0px 10px;
  margin: 0px auto;
  margin-top: 1rem;
}

.skills-container>* {
  flex: 1;
  margin-bottom: 30px;
}

.skills-image>img {
  margin-left: 80px;
  max-width: 90%;
  height: auto;
}

/* Media Query */

@media (max-width: 1456px) {
  .skills-bar {
    line-height: 4rem;
  }

  .skills-image {
    display: none;
    order: 2;
  }
}

@media (max-width: 768px) {
  .skills-container {
    flex-direction: column;
  }

  .skills-bar {
    line-height: 3rem;
    margin: 20px;
  }

  .skills-image {
    display: none;
    order: 2;
  }
}